/*App level styling*/
body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-size: 15px;
  letter-spacing: 1px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

@media screen and (max-width: 768px) {
  body {
    margin: 0;
    padding: 0;
    height: 100%;
    font-size: 15px;
    letter-spacing: 1px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow: scroll;
  }
}

html {
  height: 100%;
}

#root {
  height: 100%;
  background: #EB3349;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #F45C43, #EB3349); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

