.resumeBtn {
	box-shadow: 0px 10px 18px 1px #0d1c05;
	background:linear-gradient(to bottom, #89e05a 5%, #7dff37 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#89e05a', endColorstr='#7dff37',GradientType=0);
	background-color:#89e05a;
	border-radius:26px;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:'Jua', sans-serif;
	font-size:15px;
	padding:10px 15px;
	text-decoration:none;
	text-shadow:0px 1px 0px #000000;
}
.resumeBtn:hover {
	cursor:pointer;
	color:#ffffff;
  text-decoration:none;
	text-shadow:0px 1px 0px #000000;
}
.resumeBtn:active {
	position:relative;
	top:2px;
}


/* Navigation styling--------------------------------------->*/
.navigation{
  width: 100%;
  font-family: 'Jua', sans-serif;
  font-size: 1.4rem;
  background: #EB3349;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #F45C43, #EB3349); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.navigation a {
  padding: 10px 20px;
  color: white;
  letter-spacing: 3px;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4);
}

.navigation a:hover {
  text-decoration: none;
}

.navigation a:focus {
  font-size: 1.9rem;
} 

/*Welcome page style--------------------------------------------->*/
.content {
  position: relative;
  top: 50%;
  text-align: center;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4);
}

.content::before {
  color: white;
  content: "I'm";
  font-size: 1.5rem;
  font-family: 'Jua', sans-serif;
  letter-spacing: 4px;
}

h1 {
  color:white;
  text-shadow: 0px 4px 2px rgba(0,0,0,0.4),
               0px 8px 13px rgba(0,0,0,0.2),
               0px 18px 23px rgba(0,0,0,0.1);
  letter-spacing: 8px;
  font-weight: 700;
  font-size: 4em;
  font-family: 'Jua', sans-serif;
}

.usp{
  font-family: 'Ubuntu Mono', monospace;
  font-size: 1.7em;
  letter-spacing: 6px;
}

.time {
  position: relative;
  padding: 15px 0;
  margin: 0 auto;
  width: 45%;
  top: 20%;
  font-size: 1.2rem;
  letter-spacing: 5px;
  text-align: center;
  font-family: 'Jua', sans-serif;
  color: white;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4);
 box-shadow: 0px 10px 18px 1px #0d1c05;
  border-radius: 10px;

}

.morning{
  background-color: #FEE140;
  background-image: linear-gradient(180deg, #FEE140 0%, #FA709A 100%);
}

.noon {
  background-color: #FBAB7E;
  background-image: linear-gradient(180deg, #FBAB7E 0%, #F7CE68 100%);
}

.evening {
  background: #ff7e5f;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(180deg, #feb47b, #ff7e5f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.night {
  background: #0F2027;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

/* About page styling--------------------------------->*/

.intro{
  background-color: #EB3349;  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #F45C43, #EB3349); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  margin: 20px;
  color: white;
  text-align: center;
  border-radius: 15px;
  font-family: 'Jua', sans-serif;
  box-shadow: 2px 8px 4px 0px #0d1c05;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4);
}

.card{
  box-shadow: 0px 10px 18px 1px #0d1c05;
  border: 0;
  padding-left: 0;
  padding-right:0;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 20px;
}

.avatar {
  max-width: 100%;
  border-radius: inherit;
}

.textColor{
  color: #5e003e;
  margin-left: 30px;
}

/* Routing animation ------------------------------------->*/

.page {
  position: absolute;
  left: 15px;
  right: 15px;
  padding: 80px 0 50px 0;
}

.page-enter {
  opacity: 0;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.page-enter-active {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}

.page-exit {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.page-exit-active {
  opacity: 0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}


/*About page children styling---------------------------------->*/

.renderAbout {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 480px){
  .renderAbout {
    width: 100%;
    height: 20em;
  }
}

.renderAbout-enter {
  opacity: 0;
}
.renderAbout-enter-active {
  opacity: 1;
  transition: opacity 900ms
}
.renderAbout-exit {
  opacity: 0;
}
.renderAbout-exit-active {
  opacity: 1;
  transition: opacity 900ms
}

.toggleChildBtn {
  text-align: center;
  position: relative;
  bottom: 25px;
}

.toggleChildBtn button {
  background-color: #EB3349;  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #F45C43, #EB3349);
  color: white;
  text-align: center;
  width: 5rem;
  height: 3rem;
  font-size: 1.2rem;
  border-color: transparent;
  font-family: 'Jua', sans-serif;
  box-shadow: 0px 4px 3px 2px #0d1c05;
  text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.nextBtn {
  border-radius: 0 15px 15px 0;
  margin-left: 1px;
}

.prevBtn {
  border-radius: 15px 0 0 15px;
  margin-right: 1px;
}

.toggleChildBtn button:hover {
  border-color: transparent;
}

.toggleChildBtn button:active, .toggleChildBtn button:focus {
  border-color: transparent;
  outline: 0;
  background-color: #EB3349;
}

.toggleChildBtn button:active {
  position: relative;
  top: 1px;
}




.statsContainer {
	padding-top: 70px;
}

.statsCard {
	box-shadow: 5px 10px 18px 1px #0d1c05;
	background:linear-gradient(135deg, #98db63 0%,#6fca74 100%);
	border-radius: 15px;
	border-color: transparent;
}

.squareItem {
	margin-top: 30px;
    position: relative;
}

.squareInnerContainer {
	padding: 50px 15px 35px;
	text-align: center;
}

.squareInnerContainer h2 {
	font-size: 5rem;
	margin-bottom: 25px;
	visibility: hidden;
	font-weight: 600;
	text-shadow: 2px 5px 8px rgba(0, 0, 0, 1);
	color: #eb3548;
}

.animateValue {
	-webkit-animation-name: valueAppear;
	        animation-name: valueAppear;
	-webkit-animation-duration: 0.4s;
	        animation-duration: 0.4s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

@-webkit-keyframes valueAppear {
	from { -webkit-transform: scale(0); transform: scale(0); visibility: hidden;}
	to { -webkit-transform: scale(1); transform: scale(1); visibility: visible;}
}

@keyframes valueAppear {
	from { -webkit-transform: scale(0); transform: scale(0); visibility: hidden;}
	to { -webkit-transform: scale(1); transform: scale(1); visibility: visible;}
}

.squareInnerContainer h3 {
	font-size: 1.2rem;
	font-weight: 700;
	text-shadow: 1px 8px 4px rgba(0, 0, 0, 0.2);
    margin: 0;
	color: #eb3548;
}

.squareIcon {
	position: absolute;
	background-color: #EB3349;
	right: 40%;
	font-size: 65px;
	border-radius: 50%;
	padding: 3px;
	box-shadow: 2px 5px 10px rgba(0, 0, 0, 0.4);
	cursor: pointer;
}

.animateIcon {
	-webkit-animation-name: iconMove;
	        animation-name: iconMove;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-duration: 0.2s;
	        animation-duration: 0.2s;
}

@-webkit-keyframes iconMove {
	from{ -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
	to{ 
		-webkit-transform: translate(-150%, -90%) scale(0.55); 
		        transform: translate(-150%, -90%) scale(0.55);
	}
}

@keyframes iconMove {
	from{ -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
	to{ 
		-webkit-transform: translate(-150%, -90%) scale(0.55); 
		        transform: translate(-150%, -90%) scale(0.55);
	}
}

